.controls {
	display: flex;
	justify-content: space-evenly;
	transform: translateY(-2.5em);
	position: relative;
	.icon {
		color: var(--clr-secondary);
		display: inline-block;
		position: relative;
		transform: translateY(10%);
		transition: color 0.3s;
	}

	.favourite {
		.icon {
			color: #da1212 !important;
		}
	}

	.btn {
		font-size: var(--fz-3);
		position: relative;
		padding: var(--spacer-1) var(--spacer-1);
		&::before {
			content: '';
			position: absolute;
			width: 0em;
			height: 0em;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%) scale(0);
			background-color: #fff;
			opacity: 0;
			border-radius: 50%;
			transition: width 0.3s, height 0.3s, opacity 0.3s, transform 0.3s;
			box-shadow: 8px 23px 55px 1px rgba(211, 216, 222, 1);
		}
		&:hover,
		&:focus-visible {
			outline: none;

			.icon {
				color: var(--clr-accent);
			}
			&::before {
				width: var(--spacer-3);
				height: var(--spacer-3);
				opacity: 0.8;
				transform: translate(-50%, -50%) scale(1.25);
			}
		}
	}
	.audio-container {
		position: relative;
	}
}

.current-time {
	transform: translateY(0.75em);
	font-weight: 700;
	font-size: var(--fz-1);
	margin: -1em 0;
	color: var(--clr-accent);
}

.full-time {
	transform: translateY(2.3em);
	margin-left: auto;
	width: fit-content;
	font-weight: 700;
	color: var(--clr-accent);

	@media (min-width: 30rem) {
		transform: translateY(3em);
	}
}
